/* ========== PRINCIPLES SECTION STYLING ========== */
.principles-container {
  margin-top: 1.5rem;
}

/* Individual Principle Card */
.principle {
  margin-bottom: 1rem;
  padding: 1rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 128, 128, 0.2);
  transition: all 0.3s ease-in-out;
}

.principle:hover {
  background: #eefaf8;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 128, 128, 0.3);
}

/* Principle Titles */
.principle h3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  color: #008080;
  margin-bottom: 0.5rem;
  transition: color 0.3s ease;
}

.principle h3:hover {
  color: #005959;
}

/* Expand/Collapse Indicator */
.principle h3 span {
  font-size: 1.2rem;
  font-weight: bold;
}

/* Principle Description */
.principle p {
  margin: 0.5rem 0;
  font-weight: 500;
}

/* Expanded Details List */
.principle ul {
  padding-left: 1.5rem;
  margin-top: 0.5rem;
  list-style-type: square;
  color: #333;
}

.principle ul li {
  margin-bottom: 0.5rem;
  font-weight: 400;
}

/* Responsive Styling */
@media (max-width: 768px) {
  .principle {
    padding: 0.8rem;
  }
}

@media (max-width: 480px) {
  .principle {
    padding: 0.6rem;
  }

  .principle h3 {
    font-size: 1rem;
  }
}