/* DeFi Section */
.defi-container {
  margin-top: 1.5rem;
}

/* Individual DeFi Topics */
.defi-topic {
  margin-bottom: 1rem;
  padding: 1rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
}

.defi-topic:hover {
  background: #eefaf8;
  transform: translateY(-2px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
}

/* Topic Title */
.defi-topic h3 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  color: #008080;
  margin-bottom: 0.5rem;
  transition: color 0.3s ease;
}

.defi-topic h3:hover {
  color: #005959;
}

/* Expand/Collapse Indicator */
.defi-topic h3 span {
  font-size: 1.2rem;
  font-weight: bold;
}

/* DeFi Topic Description */
.defi-topic p {
  margin: 0.5rem 0;
}

/* Expanded Details List */
.defi-topic ul {
  padding-left: 1.5rem;
  margin-top: 0.5rem;
  color: #333;
}

.defi-topic ul li {
  margin-bottom: 0.5rem;
}

/* Responsive Styling */
@media (max-width: 768px) {
  .defi-topic {
    padding: 0.8rem;
  }
}

@media (max-width: 480px) {
  .defi-topic {
    padding: 0.6rem;
  }

  .defi-topic h3 {
    font-size: 1rem;
  }
}