/* ========== GENERAL STYLING ========== */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  color: white;
  scroll-behavior: smooth;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow-x: hidden;
}

/* ========== BACKGROUND EFFECTS ========== */
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2e2e2e, #1a1a1a, #3d3d3d, #262626);
  background-size: 400% 400%;
  animation: metallicShift 10s infinite alternate ease-in-out;
  z-index: -2; /* Ensures it’s behind all content */
}

body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.05) 1%, transparent 70%);
  background-size: 50px 50px;
  animation: gridGlow 6s infinite alternate ease-in-out;
  z-index: -1; /* Ensures it’s above the gradient but behind content */
}

/* ========== ANIMATIONS ========== */
@keyframes metallicShift {
  0% {
    background-position: 0% 50%;
    filter: brightness(1.1) contrast(1);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.3) contrast(1.1);
  }
  100% {
    background-position: 0% 50%;
    filter: brightness(1.1) contrast(1);
  }
}

@keyframes gridGlow {
  0% { opacity: 0.03; filter: hue-rotate(0deg); }
  50% { opacity: 0.07; filter: hue-rotate(15deg); }
  100% { opacity: 0.03; filter: hue-rotate(0deg); }
}

/* ========== APP LAYOUT ========== */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-grow: 1;
  position: relative;
  z-index: 1; /* Ensures all content is above background */
}

/* ========== MAIN CONTENT ========== */
main {
  width: 100%;
  max-width: 1200px;
  margin-top: 80px; /* Adjust for header height */
  flex-grow: 1;
  position: relative;
  z-index: 2; /* Ensures content is above background */
  border-radius: 10px;
  padding: 2rem;
}

/* ========== HEADER ========== */
header {
  position: fixed;
  top: 0;
  width: 100%;
  background: rgba(0, 128, 128, 0.9);
  padding: 1rem;
  text-align: center;
  z-index: 1000;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

/* ========== FOOTER ========== */
footer {
  text-align: center;
  padding: 1rem;
  background: #008080;
  color: white;
  width: 100%;
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 2; /* Ensures it's above background */
}

/* ========== RESPONSIVE DESIGN ========== */
@media (max-width: 768px) {
  main {
    padding-top: 100px;
  }

  header {
    padding: 0.8rem;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 1rem;
    font-size: 0.9rem;
  }

  footer {
    font-size: 0.9rem;
  }
}